import React from 'react';
import { useNavigate } from 'react-router-dom';
import './LoginPage.css';
import roblesLogo from '../assets/LOGO-ROBLES.png';
import facebookIcon from '../assets/facebook-icon.png';
import googleIcon from '../assets/google-icon.png';

const LoginPage = () => {
  const navigate = useNavigate();

  const handleLogin = (e) => {
    e.preventDefault();

    // Aquí puedes validar credenciales si lo deseas
    // Por ahora, redirige directo al dashboard
    navigate('/dashboard');
  };

  return (
    <div className="login-wrapper">
      <div className="login-box">
        <img src={roblesLogo} alt="Residencial Los Robles" className="logo" />
        <h2>Iniciar sesión</h2>
        <p className="subtitle">
          ¿Es tu primera vez? <button className="link-button">Regístrate</button>
        </p>

        <form onSubmit={handleLogin}>
          <label>Email *</label>
          <input type="email" placeholder="Email" required />

          <label>Contraseña *</label>
          <input type="password" placeholder="Contraseña" required />

          <button type="button" className="link-button">¿Olvidaste la contraseña?</button>

          <button type="submit">Iniciar sesión</button>
        </form>

        <div className="divider">o conéctate con</div>

        <div className="social-login">
          <img src={facebookIcon} alt="Facebook login" />
          <img src={googleIcon} alt="Google login" />
        </div>
      </div>
    </div>
  );
};

export default LoginPage;

